	module.exports = {"data":{"markdownRemark":{"html":"<p>それは特に暑く、並外れているようです。都市の気候は暑くなり始めました。経済発展の結果であるかどうか、特に驚くべき熱を放ち、環境への影響を想像することができるエアコンの大幅な増加であるのだろうかと思います。</p>\n<p>北朝鮮の金日成（キム・イルソン）大統領の死は、新しい時代の始まりを意味するかもしれない、と報じた。最近では、主に国際社会内と2人の北朝鮮内に一連の問題がありました。この事件がこれらの問題の発展にどのような影響を及ぼすかが観察されるべきである。おそらくすぐに</p>\n<p>国内の政治的安定の観点からの新しい展開。</p>\n<p>夕方に雨が降り、天気がはるかに低かった。夜に散歩に出かけて、ちょっとした詩を聞く：</p>\n<p>ナイトツアー長安街</p>\n<p>雨が降ってネットが首都にあり、緑の壁が赤だった。光を歩き、揺れ、手を合わせてアーチを凝縮させます。それは、世界は軽く、人生で笑うことを意味します。乗客は急いで孤独になり、彼らは彼らが知っていることを知っている。</p>","id":"/home/swain/workspace/political-life/src/pages/ja/0120.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ja/0120.md","frontmatter":{"title":"7月10日 日曜日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ja/0120.md absPath of file >>> MarkdownRemark"}}

